<template>
    <BaseModal
        name="share"
        :mobileMode="true"
        :closeOutside="true"
    >
        <template v-slot:title>
            <span v-html="data.title"></span>
        </template>
        <template v-slot:content>
            <div v-if="data.description" v-html="data.description" class="description"></div>
            <div class="box">
                <div class="canvas-box">
                    <canvas id="qr-canvas"></canvas>
                </div>

                <div class="buttons">
                    <IconButton :title="t('modals.share.share')"
                        class="invert secondary size-l"
                        icon="share" @click="share"/>

                    <IconButton :title="t('modals.share.copy')"
                        class="invert secondary size-l"
                        icon="copy" @click="copy"/>
                </div>
            </div>
        </template>
    </BaseModal>
</template>

<script setup>
import QRCode from 'qrcode';
import { onMounted, reactive } from 'vue';
import { useI18n } from 'vue-i18n';

import IconButton from '/components/ui/IconButton.vue';
import Button from '/components/ui/Button.vue';
import BaseModal from '/components/modals/BaseModal.vue';
import stores from '@/stores';

import { copyText, message } from '/utils/utils';

const { t } = useI18n();

const data = reactive({
});

const tg = stores.tg();
const modals = stores.modals();
const locale = stores.locale();

const close = () => {
    modals.close('share');
};

const copy = () => {
    copyText(data.url);
    message.success(data.copied);
};

const share = () => {
    if (window.Telegram?.WebApp) {
        const external_url = `https://telegram.me/share/url?url=${data.url}&text=${data.message}`;

        tg.openLink({
            external_url
        });
    }

    close();
};

const createQr = () => {
    const canvas = document.getElementById('qr-canvas');

    if (!canvas) {
        return;
    }

    QRCode.toCanvas(canvas, data.url, {
        color: {
            dark: '#101010',
            light: '#ffffff'
        },
        width: 204,
        height: 204
    });
};

onMounted(() => {
    const payload = modals.getData('share');

    data.url = payload?.url;
    data.message = payload?.message;
    data.description = payload?.description;
    data.title = locale.title(payload?.title);
    data.copied = payload?.copied;

    createQr();
});

</script>

<style lang="scss" scoped>
    .description {
        text-align: center;
        font: var(--font-body-1);
        color: var(--dark-500);
    }

    .box {
        display: flex;
        flex-direction: row;
        gap: 8px;
    }

    .canvas-box {
        width: 204px;
        height: 204px;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 30px;
        flex-shrink: 0;
        canvas {
            width: 204px;
            height: 204px;
            border-radius: 10px;
            border: solid 1px var(--primary-500);
        }
    }

    .buttons {
        display: flex;
        flex-shrink: 0;
        flex-direction: column;
        height: 204px;
        gap: 8px;
        align-items: center;
        justify-content: end;
        flex: 1 1 100%;
        button {
            width: 100%;
            flex: 1 1 50%;
        }
    }
</style>
